import React from 'react';
import YMLBoons from '../core-rules/boons/boons.yml';
import SearchBarBanesBoons from '../components/searchbar-banes-boons';

import NavLayout from '../layouts/nav-layout';

import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  boonsContainer: {
    fontSize: '16px'
  }
})

const boonAttributes = [
  'Alteration',
  'Creation',
  'Learning',
  'Movement',
  'Entropy',
  'Logic',
  'Energy',
  'Influence',
  'Presence',
  'Prescience',
  'Protection',
  'Perception',
  'Learning'
]

const BoonsPage = () => {
  const classes = useStyles();

  return (
  <NavLayout>
    <SearchBarBanesBoons attributeList={boonAttributes} />
    <div className={classes.boonsContainer} style={{ marginLeft: `24px`}}>
      {YMLBoons.map((data, index) => {
        return (
          <div className='boon-section'> 
            <h2>{data.name}</h2>
            <p><strong>Duration: </strong>{data.duration}</p>
            <p><strong>Invocation Time: </strong>{data.invocationTime}</p>
            <p><strong>Power Level: </strong>{data.power}</p>
            <p><strong>Attributes: </strong>{data.attribute}</p>
            <p><strong>Attack: </strong></p>
            <p><strong>Description</strong></p>
            <p>{data.description}</p>
            <p><strong>Effect</strong></p>
            <p>{data.effect}</p>
            <Divider /> 
          </div>
        )
      })}
    </div>
  </NavLayout>
  );
}


export default BoonsPage;

